    <div class="searchHero">
      <el-input placeholder="请输入武将名称" v-model="hero" clearable></el-input>
      <el-button type="primary" @click="confirmHero">确定</el-button>
    </div>

    <!-- 图片轮播 -->
    <char-img></char-img>
  </div>
</template>


<script>
import charImg from "@/views/swiper.vue";

export default {
  components: {
    charImg
  },
  data() {
    return {
      hero: ""
    };
  },
  methods: {
    confirmHero(){
      console.log('yes')
    }
  },
  mounted() {}
};
</script>

<style lang="" scoped>
.atlas {
  margin-top: 20px;
}
.searchHero{
  /* width: 200px; */
  display: flex;
  justify-content: space-around;
  margin-bottom: 10px;

}

</style>



