import Vue from 'vue'
import Router from 'vue-router'
import home from '@/components/home'
import headlines from '@/components/headlines/headlines'
import atlas from '@/components/atlas/atlas'

Vue.use(Router)

export default new Router({
  mode:'history',
  routes: [
    {
      path: '/',
      name: 'home',
      component: home,
      children: [
        {
          path: '/',
          redirect: '/headlines'
        },
        {
          path: '/headlines',
          name: 'headlines',
          component: headlines
        },
        {
          path: '/atlas',
          name: 'atlas',
          component: atlas
        },
        {
          path: '/sky',
          name: 'sky',
          component: () => import('@//components/video/sky.vue')
        },
        {
          path: '/land',
          name: 'land',
          component: () => import('@//components/video/land.vue')
        },
        {
          path: '/ssr',
          name: 'ssr',
          component: () => import('@//components/ssr/ssr.vue')
        },
        {
          path: '/recommend',
          name: 'recommend',
          component: () => import('@//components/recommend/recommend.vue')
        },
        {
          path: '/biglevel',
          name: 'biglevel',
          component: () => import('@//components/biglevel/biglevel.vue')
        },
        {
          path: '/smolllevel',
          name: 'smolllevel',
          component: () => import('@//components/smolllevel/smolllevel.vue')
        },
        {
          path: '/shabilevel',
          name: 'shabilevel',
          component: () => import('@//components/shabilevel/shabilevel.vue')
        },
        {
          path: '/video/:id',
          name: 'video',
          component: () => import('@//components/video/video.vue')
        }
      ]
    },

  ]
})



