<template>
  <div class="headlines">
    <!-- 每日答案 -->

    <marquee behavior="scroll">
      <p class="answer">{{today + ',答案选' + 'C' }}</p>
    </marquee>

    <!-- 图片轮播 -->

    <char-img></char-img>
  </div>
</template>


<script>
import encapsulation from "@/encapsulation/index.js";

import charImg from "@/views/swiper.vue";

export default {
  components: {
    charImg
  },
  data() {
    return {
      today: ""
    };
  },
  methods: {},
  mounted() {
    this.today = encapsulation.today();
  }
};
</script>

<style lang="">
.answer {
  color: #d0025b;
}
.marquee {
  width: 1000px;
  margin: 0 auto;
}
</style>



