      <el-menu-item index="ssr">SSR</el-menu-item>
      <!-- <el-menu-item index="report">禁将</el-menu-item> -->

       <el-submenu index="3">
        <template slot="title">禁将方案</template>
        <el-menu-item index="biglevel">禁将方案一</el-menu-item>
        <el-menu-item index="smolllevel">禁将方案二</el-menu-item>
        <el-menu-item index="shabilevel">选了就赢</el-menu-item>
      </el-submenu>
    </el-menu>

    <router-view class="sonviews"></router-view>
  </div>
</template>

<script>
export default {
  data() {
    return {
      activeIndex2: this.$route.name
    };
  },
  methods: {
    handleSelect(key, keyPath) {
      //  console.log(key);
      this.$router.push({
        path:'/' + key
      })
    }
  }
};
</script>


<style lang="" scoped>

.el-menu-demo {
  display: flex;
  justify-content: center;
}
.sonviews {
  width: 1000px;
  margin: 0 auto;
}
.el-menu--popup{
  min-width: 110px;
}
</style>



